import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { toast } from "react-hot-toast";
import { projectAPI, handleAPIError } from "../services/api";
import AdminView from "./AdminView";
import UserView from "./UserView";
import { useAuth } from "../contexts/AuthContext";
import { ChatInterfaceSkeleton } from "./skeleton/SkeletonComponents";

const ProjectDetail = () => {
  const { projectId } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [project, setProject] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadProjectData();
  }, [projectId, navigate]);

  const loadProjectData = async () => {
    try {
      setLoading(true);

      // ÏùºÎ∞ò ÏÇ¨Ïö©ÏûêÏù∏ Í≤ΩÏö∞ Í≥†Ï†ïÎêú ÌîÑÎ°úÏ†ùÌä∏ÎÇò Í∏∞Î≥∏ ÌîÑÎ°úÏ†ùÌä∏Î•º ÏÇ¨Ïö©
      let projectData;
      if (user?.role === "user") {
        // ÏùºÎ∞ò ÏÇ¨Ïö©ÏûêÏö© Í∏∞Î≥∏ ÌîÑÎ°úÏ†ùÌä∏ ÏÇ¨Ïö© (Ïã§Ï†úÎ°úÎäî Í¥ÄÎ¶¨ÏûêÍ∞Ä ÏÑ§Ï†ïÌïú Í∏∞Î≥∏ ÌîÑÎ°úÏ†ùÌä∏)
        try {
          projectData = await projectAPI.getProject(projectId || "default");
        } catch (error) {
          // Í∏∞Î≥∏ ÌîÑÎ°úÏ†ùÌä∏Í∞Ä ÏóÜÏúºÎ©¥ Îπà ÌîÑÎ°úÏ†ùÌä∏ ÏÉùÏÑ±
          projectData = {
            projectId: "chat",
            name: "AI Ï±ÑÌåÖ",
            description: "AIÏôÄ ÏûêÏú†Î°≠Í≤å ÎåÄÌôîÌïòÏÑ∏Ïöî",
            promptCards: [],
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
          };
        }
      } else {
        projectData = await projectAPI.getProject(projectId);
      }

      setProject(projectData);
    } catch (error) {
      console.error("ÌîÑÎ°úÏ†ùÌä∏ Î°úÎìú Ïã§Ìå®:", error);
      const errorInfo = handleAPIError(error);
      toast.error(errorInfo.message);

      // Í∂åÌïúÏóê Îî∞Î•∏ Î¶¨Îã§Ïù¥Î†âÌä∏
      if (user?.role === "admin") {
        navigate("/projects");
      } else {
        navigate("/dashboard");
      }
    } finally {
      setLoading(false);
    }
  };


  if (loading) {
    return <ChatInterfaceSkeleton />;
  }

  if (!project) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center transition-colors duration-200">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
            ÌîÑÎ°úÏ†ùÌä∏Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§
          </h2>
          <button
            onClick={() => {
              if (user?.role === "admin") {
                navigate("/projects");
              } else {
                navigate("/dashboard");
              }
            }}
            className="inline-flex items-center px-4 py-2 text-sm font-medium rounded-md text-blue-700 dark:text-blue-300 bg-blue-100 dark:bg-blue-900 hover:bg-blue-200 dark:hover:bg-blue-800 focus:outline-none transition-colors duration-200"
          >
            {user?.role === "admin" ? "ÌîÑÎ°úÏ†ùÌä∏ Î™©Î°ùÏúºÎ°ú" : "ÎåÄÏãúÎ≥¥ÎìúÎ°ú"}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen w-full bg-gray-50 dark:bg-dark-primary transition-all duration-500">
      {user?.role === "admin" ? (
        // Í¥ÄÎ¶¨Ïûê Î™®Îìú: ÌîÑÎ°¨ÌîÑÌä∏ Ïπ¥Îìú Í¥ÄÎ¶¨ + Ï±ÑÌåÖ
        <AdminView projectId={projectId} projectName={project.name} />
      ) : (
        // ÏÇ¨Ïö©Ïûê Î™®Îìú: Ï±ÑÌåÖÎßå
        <UserView projectId={projectId} projectName={project.name} />
      )}
    </div>
  );
};

export default ProjectDetail;
