import { toast } from "react-hot-toast";

/**
 * ÌÖçÏä§Ìä∏Î•º ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÌïòÎäî Ïú†Ìã∏Î¶¨Ìã∞ Ìï®Ïàò
 * @param {string} text - Î≥µÏÇ¨Ìï† ÌÖçÏä§Ìä∏
 * @param {string} successMessage - ÏÑ±Í≥µ Ïãú ÌëúÏãúÌï† Î©îÏãúÏßÄ
 * @param {string} errorMessage - Ïã§Ìå® Ïãú ÌëúÏãúÌï† Î©îÏãúÏßÄ
 * @returns {Promise<boolean>} - ÏÑ±Í≥µ Ïó¨Î∂Ä
 */
export const copyToClipboard = async (
  text,
  successMessage = "ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§!",
  errorMessage = "Î≥µÏÇ¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§."
) => {
  try {
    await navigator.clipboard.writeText(text);
    toast.success(successMessage);
    return true;
  } catch (error) {
    console.error("Î≥µÏÇ¨ Ïã§Ìå®:", error);
    toast.error(errorMessage);
    return false;
  }
};

/**
 * Ï†úÎ™©Îßå Ï∂îÏ∂úÌïòÏó¨ Î≥µÏÇ¨ÌïòÎäî Ìï®Ïàò
 * @param {string} text - Ï†ÑÏ≤¥ ÌÖçÏä§Ìä∏
 * @returns {Promise<boolean>} - ÏÑ±Í≥µ Ïó¨Î∂Ä
 */
export const copyTitlesOnly = async (text) => {
  const extractedTitles = extractTitles(text);
  const textToCopy =
    extractedTitles.length > 0 ? extractedTitles.join("\n") : text;
  return copyToClipboard(textToCopy, "Ï†úÎ™©Ïù¥ ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§!");
};

/**
 * ÌÖçÏä§Ìä∏ÏóêÏÑú Ï†úÎ™©ÏùÑ Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * @param {string} text - ÏõêÎ≥∏ ÌÖçÏä§Ìä∏
 * @returns {Array<string>} - Ï∂îÏ∂úÎêú Ï†úÎ™© Î∞∞Ïó¥
 */
export const extractTitles = (text) => {
  const titles = [];

  // 1. Î≤àÌò∏ ÌòïÏãù: "1. [Ï†úÎ™©]"
  const numberedMatches = text.match(/^\d+\.\s+(.+?)(?=\n\s*-|$)/gm);
  if (numberedMatches) {
    numberedMatches.forEach((match) => {
      const title = match.replace(/^\d+\.\s+/, "").trim();
      if (title && !title.includes("ÌíàÏßà ÌèâÍ∞Ä")) {
        titles.push(title);
      }
    });
  }

  // 2. Bullet ÌòïÏãù: "‚Ä¢ [Ï†úÎ™©]"
  if (titles.length === 0) {
    const bulletMatches = text.match(/‚Ä¢ "([^"]+)"/g);
    if (bulletMatches) {
      bulletMatches.forEach((match) => {
        const title = match.replace(/‚Ä¢ "(.+)"/, "$1").trim();
        if (title) {
          titles.push(title);
        }
      });
    }
  }

  // 3. ÏùºÎ∞ò Bullet ÌòïÏãù
  if (titles.length === 0) {
    const simpleBulletMatches = text.match(/‚Ä¢ ([^\n]+)/g);
    if (simpleBulletMatches) {
      simpleBulletMatches.forEach((match) => {
        const title = match.replace("‚Ä¢ ", "").trim();
        if (title && !title.includes("Ïù¥Ïú†:") && title.length < 100) {
          titles.push(title);
        }
      });
    }
  }

  return titles;
};
