import { useState, useEffect, useRef, useCallback } from "react";
import { toast } from "react-hot-toast";
import { copyToClipboard } from "../utils/clipboard";
import { useOrchestration } from "./useOrchestration";
import { useWebSocket } from "./useWebSocket";
import { crewAPI } from "../services/api";

/**
 * Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ AI ÏùëÎãµÏùÑ ÌååÏã±ÌïòÍ≥† UIÏóê ÎßûÎäî Î©îÏãúÏßÄ Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
 */
const processMultiAgentResponse = (result) => {
  if (!result) {
    console.error("Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ ÏùëÎãµ Ïò§Î•ò: Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§", result);
    return {
      id: "error-" + Date.now(),
      type: "assistant",
      content: "Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.",
      timestamp: new Date(),
      isError: true,
    };
  }

  // Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ Í≤∞Í≥º Íµ¨ÏÑ±
  const agentResults = result.agentResults || {};
  const finalTitles = result.finalTitles || {};
  const tokenUsage = result.tokenUsage || 0;

  // ÏóêÏù¥Ï†ÑÌä∏Î≥Ñ Í≤∞Í≥ºÎ•º Ìè¨Îß∑ÌåÖ
  const agentSummary = Object.keys(agentResults).map(agentType => {
    const agentResult = agentResults[agentType];
    const titles = finalTitles[agentType] || [];
    
    return {
      agentType,
      agentName: getAgentName(agentType),
      result: agentResult?.content || "Í≤∞Í≥º ÏóÜÏùå",
      titles: titles,
      tokenCount: agentResult?.tokenCount || 0
    };
  });

  // Ï†ÑÏ≤¥ Ï†úÎ™© Î™©Î°ù ÏÉùÏÑ±
  const allTitles = Object.values(finalTitles).flat();

  return {
    id: "multi-agent-" + Date.now(),
    type: "assistant",
    content: formatMultiAgentContent(agentSummary, allTitles),
    timestamp: new Date(),
    // Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ ÌäπÌôî Îç∞Ïù¥ÌÑ∞
    isMultiAgent: true,
    agentResults: agentSummary,
    allTitles: allTitles,
    tokenUsage: tokenUsage,
  };
};

/**
 * ÏóêÏù¥Ï†ÑÌä∏ ÌÉÄÏûÖÏùÑ ÌïúÍµ≠Ïñ¥ Ïù¥Î¶ÑÏúºÎ°ú Î≥ÄÌôò
 */
const getAgentName = (agentType) => {
  const agentNames = {
    journalism: "üì∞ Ï†ÄÎÑêÎ¶¨Ï¶ò Ï∂©Ïã§Ìòï",
    balanced: "‚öñÔ∏è Í∑†ÌòïÏû°Ìûå ÌõÑÌÇπÌòï", 
    click: "üéØ ÌÅ¥Î¶≠Ïú†ÎèÑÌòï",
    seo: "üîç SEO/AEO ÏµúÏ†ÅÌôîÌòï",
    social: "üì± ÏÜåÏÖúÎØ∏ÎîîÏñ¥ Í≥µÏú†Ìòï"
  };
  return agentNames[agentType] || `ü§ñ ${agentType}`;
};

/**
 * Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ Í≤∞Í≥ºÎ•º UI ÌëúÏãúÏö© ÌÖçÏä§Ìä∏Î°ú Ìè¨Îß∑ÌåÖ
 */
const formatMultiAgentContent = (agentSummary, allTitles) => {
  let content = "üöÄ **Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ Î∂ÑÏÑù ÏôÑÎ£å**\n\n";
  
  // Ï†ÑÏ≤¥ Ï†úÎ™© ÏöîÏïΩ
  if (allTitles.length > 0) {
    content += "üìã **ÏÉùÏÑ±Îêú Ï†úÎ™© Î™©Î°ù:**\n";
    allTitles.forEach((title, index) => {
      content += `${index + 1}. ${title}\n`;
    });
    content += "\n";
  }

  // ÏóêÏù¥Ï†ÑÌä∏Î≥Ñ ÏÉÅÏÑ∏ Í≤∞Í≥º
  content += "üîç **ÏóêÏù¥Ï†ÑÌä∏Î≥Ñ Î∂ÑÏÑù Í≤∞Í≥º:**\n\n";
  agentSummary.forEach(agent => {
    content += `### ${agent.agentName}\n`;
    if (agent.titles.length > 0) {
      content += "**ÏÉùÏÑ± Ï†úÎ™©:**\n";
      agent.titles.forEach(title => {
        content += `‚Ä¢ ${title}\n`;
      });
    }
    content += `**ÌÜ†ÌÅ∞ ÏÇ¨Ïö©Îüâ:** ${agent.tokenCount}Í∞ú\n\n`;
  });

  return content;
};

/**
 * Ï±ÑÌåÖ Í∏∞Îä•ÏùÑ ÏúÑÌïú Ïª§Ïä§ÌÖÄ ÌõÖ
 * @param {string} projectId - ÌîÑÎ°úÏ†ùÌä∏ ID
 * @param {string} projectName - ÌîÑÎ°úÏ†ùÌä∏ Ïù¥Î¶Ñ
 * @param {Array} promptCards - ÌîÑÎ°¨ÌîÑÌä∏ Ïπ¥Îìú Î∞∞Ïó¥
 * @returns {Object} - Ï±ÑÌåÖ Í¥ÄÎ†® ÏÉÅÌÉúÏôÄ Ìï®ÏàòÎì§
 */
export const useChat = (projectId, projectName, promptCards = []) => {
  const [messages, setMessages] = useState([]);
  const [inputValue, setInputValue] = useState("");
  const [copiedMessage, setCopiedMessage] = useState(null);
  const [canSendMessage, setCanSendMessage] = useState(true);
  const [inputHeight, setInputHeight] = useState(24); // ÎèôÏ†Å ÎÜíÏù¥ Í¥ÄÎ¶¨
  const [selectedModel, setSelectedModel] = useState("anthropic.claude-3-5-sonnet-20241022-v2:0");
  const streamingMessageIdRef = useRef(null);
  const currentWebSocketRef = useRef(null);
  const currentExecutionIdRef = useRef(null);
  

  const messagesEndRef = useRef(null);
  const inputRef = useRef(null);

  // ÏÇ¨Ïö©Ïûê Ïä§ÌÅ¨Î°§ ÏÉÅÌÉú Ï∂îÏ†Å
  const [isUserScrolling, setIsUserScrolling] = useState(false);
  const scrollContainerRef = useRef(null);
  const lastScrollTopRef = useRef(0);

  const {
    isExecuting: isGenerating,
    isStreaming,
    executeOrchestration,
    pollOrchestrationResult,
    resetOrchestration,
  } = useOrchestration(projectId);

  // WebSocket ÌõÖ Ï∂îÍ∞Ä
  const {
    isConnected: wsConnected,
    isConnecting: wsConnecting,
    error: wsError,
    startStreaming: wsStartStreaming,
    addMessageListener,
    removeMessageListener,
  } = useWebSocket(projectId);

  // Ï¥àÍ∏∞ ÌôòÏòÅ Î©îÏãúÏßÄ ÏÑ§Ï†ï - Ï†úÍ±∞Îê® (Îπà ÏÉÅÌÉúÎ°ú ÏãúÏûë)
  useEffect(() => {
    setMessages([]); // Îπà Î∞∞Ïó¥Î°ú ÏãúÏûë
  }, [projectName]);

  // ÏÇ¨Ïö©Ïûê Ïä§ÌÅ¨Î°§ Í∞êÏßÄ Ìï®Ïàò
  const handleScroll = useCallback(() => {
    if (!scrollContainerRef.current) return;

    const container = scrollContainerRef.current;
    const currentScrollTop = container.scrollTop;
    const maxScrollTop = container.scrollHeight - container.clientHeight;

    // ÏÇ¨Ïö©ÏûêÍ∞Ä ÏàòÎèôÏúºÎ°ú Ïä§ÌÅ¨Î°§ÌñàÎäîÏßÄ Í∞êÏßÄ
    if (Math.abs(currentScrollTop - lastScrollTopRef.current) > 2) {
      const isAtBottom = currentScrollTop >= maxScrollTop - 20;

      // ÌïòÎã®Ïóê ÏûàÏùÑ ÎïåÎßå ÏûêÎèô Ïä§ÌÅ¨Î°§ ÌóàÏö©, Í∑∏ Ïô∏Îäî ÏÇ¨Ïö©Ïûê Ïä§ÌÅ¨Î°§ Î™®Îìú
      setIsUserScrolling(!isAtBottom);
    }

    lastScrollTopRef.current = currentScrollTop;
  }, []);

  const scrollToBottom = useCallback(() => {
    // ÏÇ¨Ïö©ÏûêÍ∞Ä Ïä§ÌÅ¨Î°§ Ï§ëÏù¥ ÏïÑÎãê ÎïåÎßå ÏûêÎèô Ïä§ÌÅ¨Î°§
    if (!isUserScrolling && messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [isUserScrolling]);

  // Î©îÏãúÏßÄ Ï∂îÍ∞Ä Ïãú Ïä§ÌÅ¨Î°§ ÌïòÎã®ÏúºÎ°ú (ÏÇ¨Ïö©Ïûê Ïä§ÌÅ¨Î°§ Ï§ëÏù¥ ÏïÑÎãê ÎïåÎßå)
  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  // WebSocket Î©îÏãúÏßÄ Î¶¨Ïä§ÎÑà ÏÑ§Ï†ï
  useEffect(() => {
    const handleWebSocketMessage = (event) => {
      try {
        const data = JSON.parse(event.data);
        console.log("WebSocket Î©îÏãúÏßÄ ÏàòÏã†:", data);

        const currentStreamingId = streamingMessageIdRef.current;

        switch (data.type) {
          case "stream_start":
            console.log("WebSocket Ïä§Ìä∏Î¶¨Î∞ç ÏãúÏûë");
            break;

          case "progress":
            // ÏßÑÌñâ ÏÉÅÌô© Î°úÍ∑∏Îßå ÎÇ®Í∏∞Í≥† UI ÏóÖÎç∞Ïù¥Ìä∏Îäî Ï†úÍ±∞
            console.log(`ÏßÑÌñâ ÏÉÅÌô©: ${data.step} (${data.progress}%)`);
            break;

          case "stream_chunk":
            if (currentStreamingId) {
              setMessages((prev) => {
                const updatedMessages = [...prev];
                const streamingMsgIndex = updatedMessages.findIndex(
                  (msg) => msg.id === currentStreamingId
                );

                if (streamingMsgIndex !== -1) {
                  // Í∏∞Ï°¥ ÎÇ¥Ïö©Ïóê ÏÉà Ï≤≠ÌÅ¨ Ï∂îÍ∞Ä
                  const currentContent =
                    updatedMessages[streamingMsgIndex].content || "";

                  updatedMessages[streamingMsgIndex] = {
                    ...updatedMessages[streamingMsgIndex],
                    content: currentContent + data.content,
                    isLoading: true,
                    isStreaming: true,
                  };
                }

                return updatedMessages;
              });
              // Ïä§Ìä∏Î¶¨Î∞ç Ï§ëÏóêÎäî ÏÇ¨Ïö©ÏûêÍ∞Ä Ïä§ÌÅ¨Î°§ Ï§ëÏù¥ ÏïÑÎãê ÎïåÎßå ÏûêÎèô Ïä§ÌÅ¨Î°§
              if (!isUserScrolling) {
                scrollToBottom();
              }
            }
            break;

          case "stream_complete":
            if (currentStreamingId) {
              setMessages((prev) => {
                const updatedMessages = [...prev];
                const streamingMsgIndex = updatedMessages.findIndex(
                  (msg) => msg.id === currentStreamingId
                );

                if (streamingMsgIndex !== -1) {
                  updatedMessages[streamingMsgIndex] = {
                    ...updatedMessages[streamingMsgIndex],
                    content: data.fullContent,
                    isLoading: false,
                    isStreaming: false,
                    timestamp: new Date(),
                  };
                }

                return updatedMessages;
              });
              streamingMessageIdRef.current = null;
              scrollToBottom();
            }
            break;

          case "error":
            console.error("WebSocket Ïä§Ìä∏Î¶¨Î∞ç Ïò§Î•ò:", data.message);
            if (currentStreamingId) {
              setMessages((prev) => {
                const updatedMessages = [...prev];
                const streamingMsgIndex = updatedMessages.findIndex(
                  (msg) => msg.id === currentStreamingId
                );

                if (streamingMsgIndex !== -1) {
                  updatedMessages[streamingMsgIndex] = {
                    ...updatedMessages[streamingMsgIndex],
                    content:
                      "Î©îÏãúÏßÄ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.",
                    isLoading: false,
                    isStreaming: false,
                    isError: true,
                    timestamp: new Date(),
                  };
                }

                return updatedMessages;
              });
              streamingMessageIdRef.current = null;
            }
            toast.error(data.message);
            break;

          default:
            console.log("Ïïå Ïàò ÏóÜÎäî WebSocket Î©îÏãúÏßÄ ÌÉÄÏûÖ:", data.type);
        }
      } catch (error) {
        console.error("WebSocket Î©îÏãúÏßÄ ÌååÏã± Ïò§Î•ò:", error);
      }
    };

    if (wsConnected) {
      addMessageListener(handleWebSocketMessage);
    }

    return () => {
      if (wsConnected) {
        removeMessageListener(handleWebSocketMessage);
      }
    };
  }, [wsConnected, addMessageListener, removeMessageListener, scrollToBottom]);

  /**
   * Ïä§Ìä∏Î¶¨Î∞ç ÏùëÎãµ Ï≤òÎ¶¨ Ìï®Ïàò
   */
  const handleStreamingResponse = useCallback(
    (chunk, metadata) => {
      const currentStreamingId = streamingMessageIdRef.current;

      console.log("Ï≤≠ÌÅ¨ ÏàòÏã†:", chunk, "Ïä§Ìä∏Î¶¨Î∞ç ID:", currentStreamingId);

      if (!currentStreamingId) {
        console.error("Ïä§Ìä∏Î¶¨Î∞ç IDÍ∞Ä ÏóÜÏäµÎãàÎã§!");
        return;
      }

      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === currentStreamingId
        );

        if (streamingMsgIndex !== -1) {
          // Í∏∞Ï°¥ Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄ ÏóÖÎç∞Ïù¥Ìä∏
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content: updatedMessages[streamingMsgIndex].content + chunk,
            isLoading: true,
            isStreaming: true,
          };
          console.log(
            "Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄ ÏóÖÎç∞Ïù¥Ìä∏ ÏÑ±Í≥µ:",
            updatedMessages[streamingMsgIndex].content
          );
        } else {
          console.error("Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏùå:", currentStreamingId);
        }

        return updatedMessages;
      });

      // Ïä§ÌÅ¨Î°§ Ï°∞Ï†ï (ÏÇ¨Ïö©ÏûêÍ∞Ä Ïä§ÌÅ¨Î°§ Ï§ëÏù¥ ÏïÑÎãê ÎïåÎßå)
      if (!isUserScrolling) {
        scrollToBottom();
      }
    },
    [scrollToBottom, isUserScrolling]
  );

  /**
   * Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å Ï≤òÎ¶¨ Ìï®Ïàò
   */
  const handleStreamingComplete = useCallback(
    (result) => {
      const currentStreamingId = streamingMessageIdRef.current;

      console.log("Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å:", result, "Ïä§Ìä∏Î¶¨Î∞ç ID:", currentStreamingId);

      if (!currentStreamingId) {
        console.error("Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å Ï≤òÎ¶¨ Ï§ë IDÍ∞Ä ÏóÜÏäµÎãàÎã§!");
        return;
      }

      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === currentStreamingId
        );

        if (streamingMsgIndex !== -1) {
          // Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄ ÏôÑÎ£å Ï≤òÎ¶¨
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content: result.result,
            isLoading: false,
            isStreaming: false,
            performance_metrics: result.performance_metrics,
            model_info: result.model_info,
            timestamp: new Date(),
          };
          console.log(
            "Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å Ï≤òÎ¶¨ ÏÑ±Í≥µ:",
            updatedMessages[streamingMsgIndex].content
          );
        } else {
          console.error(
            "Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å Ï≤òÎ¶¨ Ï§ë Î©îÏãúÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏùå:",
            currentStreamingId
          );
        }

        return updatedMessages;
      });

      // Ïä§Ìä∏Î¶¨Î∞ç ID Ï¥àÍ∏∞Ìôî
      streamingMessageIdRef.current = null;

      // ÏûÖÎ†• ÌôúÏÑ±Ìôî
      console.log("WebSocket Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å - ÏûÖÎ†• ÌôúÏÑ±Ìôî");
      setCanSendMessage(true);

      // Ïä§ÌÅ¨Î°§ Ï°∞Ï†ï (Ïä§Ìä∏Î¶¨Î∞ç ÏôÑÎ£å ÏãúÏóêÎäî Ìï≠ÏÉÅ ÌïòÎã®ÏúºÎ°ú)
      scrollToBottom();
    },
    [scrollToBottom]
  );

  /**
   * Ïä§Ìä∏Î¶¨Î∞ç Ï§ëÎã® Ìï®Ïàò
   */
  const handleStopGeneration = useCallback(() => {
    console.log("ÏÉùÏÑ± Ï§ëÎã® ÏöîÏ≤≠");

    // WebSocket Ïó∞Í≤∞ Ï¢ÖÎ£å
    if (currentWebSocketRef.current) {
      currentWebSocketRef.current.close();
      currentWebSocketRef.current = null;
    }

    // ÌòÑÏû¨ Ïã§Ìñâ Ï§ëÏù∏ ÏûëÏóÖ Ï§ëÎã®
    if (currentExecutionIdRef.current) {
      // Ïó¨Í∏∞ÏÑú Ïã§Ï†ú API Ìò∏Ï∂ú Ï§ëÎã® Î°úÏßÅÏùÑ Ï∂îÍ∞ÄÌï† Ïàò ÏûàÏäµÎãàÎã§
      currentExecutionIdRef.current = null;
    }

    // Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
    const currentStreamingId = streamingMessageIdRef.current;
    if (currentStreamingId) {
      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === currentStreamingId
        );

        if (streamingMsgIndex !== -1) {
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content:
              updatedMessages[streamingMsgIndex].content +
              "\n\n[ÏÉùÏÑ±Ïù¥ Ï§ëÎã®ÎêòÏóàÏäµÎãàÎã§]",
            isLoading: false,
            isStreaming: false,
            timestamp: new Date(),
          };
        }

        return updatedMessages;
      });

      streamingMessageIdRef.current = null;
    }

    // ÏûÖÎ†• Í∞ÄÎä• ÏÉÅÌÉúÎ°ú Î≥µÏõê
    setCanSendMessage(true);

    // orchestration ÏÉÅÌÉú Î¶¨ÏÖã
    resetOrchestration();

    toast.success("ÏÉùÏÑ±Ïù¥ Ï§ëÎã®ÎêòÏóàÏäµÎãàÎã§");
  }, [resetOrchestration]);

  /**
   * ÏûÖÎ†•Ï∞Ω ÎÜíÏù¥ ÏûêÎèô Ï°∞Ï†à
   */
  const adjustInputHeight = useCallback((value) => {
    if (!value.trim()) {
      setInputHeight(24); // Í∏∞Î≥∏ ÎÜíÏù¥
      return;
    }
    
    // Ï§Ñ Ïàò Í≥ÑÏÇ∞ (ÎåÄÎûµÏ†Å)
    const lines = value.split('\n').length;
    const charBasedLines = Math.ceil(value.length / 80); // 80ÏûêÎãπ 1Ï§ÑÎ°ú Ï∂îÏ†ï
    const estimatedLines = Math.max(lines, charBasedLines);
    
    // ÎÜíÏù¥ Í≥ÑÏÇ∞ (lineHeight: 1.4, fontSize: 16px)
    let calculatedHeight;
    if (estimatedLines <= 3) {
      calculatedHeight = 24 + (estimatedLines - 1) * 22; // Í∏∞Î≥∏ + Ï∂îÍ∞Ä Ï§Ñ
    } else if (estimatedLines <= 10) {
      calculatedHeight = 150 + (estimatedLines - 6) * 15; // Ï§ëÍ∞Ñ Î≤îÏúÑ
    } else {
      calculatedHeight = Math.min(400, 150 + (estimatedLines - 6) * 12); // ÏµúÎåÄ 400px
    }
    
    setInputHeight(Math.max(24, calculatedHeight));
  }, []);
  
  /**
   * ÏûÖÎ†•Í∞í Î≥ÄÍ≤Ω Ï≤òÎ¶¨
   */
  const handleInputChange = useCallback((value) => {
    setInputValue(value);
    adjustInputHeight(value);
  }, [adjustInputHeight]);

  /**
   * Î©îÏãúÏßÄ Ï†ÑÏÜ°
   */
  const handleSendMessage = useCallback(async () => {
    console.log("Ìï¥Îì§ Ï†ÑÏÜ° Ìò∏Ï∂ú:", {
      inputValue: inputValue.trim(),
      isGenerating,
      canSendMessage,
    });

    if (!inputValue.trim() || isGenerating) {
      console.log("Ï†ÑÏÜ° Ï§ëÎã®: Ï°∞Í±¥ Î∂ÄÏ°±");
      return;
    }

    // ÏûÖÎ†• ÎπÑÌôúÏÑ±Ìôî
    console.log("ÏûÖÎ†• ÎπÑÌôúÏÑ±Ìôî");
    setCanSendMessage(false);

    const userMessage = {
      id: "user-" + Date.now(),
      type: "user",
      content: inputValue.trim(),
      timestamp: new Date(),
    };

    // Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄ ID ÏÉùÏÑ±
    const streamMsgId = "streaming-" + Date.now();
    streamingMessageIdRef.current = streamMsgId;

    console.log("ÏÉà Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄ ID ÏÉùÏÑ±:", streamMsgId);

    // Ïä§Ìä∏Î¶¨Î∞ç ÏùëÎãµÏùÑ ÏúÑÌïú Ï¥àÍ∏∞ Î©îÏãúÏßÄ
    const streamingMessage = {
      id: streamMsgId,
      type: "assistant",
      content: "",
      timestamp: new Date(),
      isLoading: true,
      isStreaming: true,
    };

    setMessages((prev) => [...prev, userMessage, streamingMessage]);
    setInputValue("");
    setInputHeight(24); // ÏûÖÎ†•Ï∞Ω ÎÜíÏù¥ Ï¥àÍ∏∞Ìôî

    // Í∏∞Ï°¥ Î©îÏãúÏßÄ + ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Î©îÏãúÏßÄÎ•º Ìè¨Ìï®Ìïú ÎåÄÌôî ÌûàÏä§ÌÜ†Î¶¨ ÏÉùÏÑ±
    const allMessages = [...messages, userMessage];
    const chatHistory = allMessages
      .filter((msg) => !msg.isLoading && !msg.isError && !msg.isStreaming)
      .map((msg) => ({
        role: msg.type === "user" ? "user" : "assistant",
        content: msg.content,
      }));

    // ÏµúÎåÄ ÎåÄÌôî Í∏∞Ïñµ ÏÑ§Ï†ï (ÏµúÍ∑º 50Í∞ú Î©îÏãúÏßÄÎ°ú ÏµúÎåÄ Î©îÎ™®Î¶¨ Ïú†ÏßÄ)
    const maxHistoryLength = 50;
    const trimmedChatHistory = chatHistory.slice(-maxHistoryLength);

    console.log("ÎåÄÌôî ÌûàÏä§ÌÜ†Î¶¨ ÏÉùÏÑ±:", {
      totalMessages: allMessages.length,
      fullHistoryLength: chatHistory.length,
      trimmedHistoryLength: trimmedChatHistory.length,
      maxHistoryLength: maxHistoryLength,
      recentHistory: trimmedChatHistory.slice(-6), // ÏµúÍ∑º 6Í∞úÎßå Î°úÍ∑∏Ïóê ÌëúÏãú
    });

    try {
      // ÌîÑÎ°¨ÌîÑÌä∏ Ïπ¥Îìú Ï†ïÎ≥¥ Ï∂îÍ∞Ä - ÌôúÏÑ±ÌôîÎêú Ïπ¥ÎìúÎßå ÌïÑÌÑ∞ÎßÅÌïòÍ≥† Î∞±ÏóîÎìú ÌòïÏãùÏóê ÎßûÍ≤å Î≥ÄÌôò
      const safePromptCards = Array.isArray(promptCards) ? promptCards : [];
      const activePromptCards = safePromptCards
        .filter((card) => card.isActive !== false && card.enabled !== false)
        .map((card) => ({
          promptId: card.promptId || card.prompt_id,
          title: card.title || "Untitled",
          prompt_text: card.prompt_text || card.content || "",
          tags: card.tags || [],
          isActive: card.isActive !== false,
          stepOrder: card.stepOrder || 0,
        }))
        .filter((card) => card.prompt_text.trim()) // ÌîÑÎ°¨ÌîÑÌä∏ ÎÇ¥Ïö©Ïù¥ ÏûàÎäî Í≤ÉÎßå
        .sort((a, b) => (a.stepOrder || 0) - (b.stepOrder || 0)); // stepOrderÎ°ú Ï†ïÎ†¨

      console.log("ÎåÄÌôî Ï†ÑÏÜ° Îç∞Ïù¥ÌÑ∞ ÌôïÏù∏:", {
        messageContent: userMessage.content,
        chatHistoryLength: trimmedChatHistory.length,
        promptCardsCount: activePromptCards.length,
        chatHistory: trimmedChatHistory,
        promptCards: activePromptCards.map((card) => ({
          id: card.promptId,
          title: card.title,
          contentLength: card.prompt_text.length,
          stepOrder: card.stepOrder,
          hasContent: !!card.prompt_text.trim(),
        })),
      });

      // WebSocket Ïó∞Í≤∞ ÌôïÏù∏ Î∞è Ïã§ÏãúÍ∞Ñ Ïä§Ìä∏Î¶¨Î∞ç ÏãúÎèÑ
      if (wsConnected) {
        console.log("WebSocketÏùÑ ÌÜµÌïú Ïã§ÏãúÍ∞Ñ Ïä§Ìä∏Î¶¨Î∞ç ÏãúÏûë");

        const success = wsStartStreaming(
          userMessage.content,
          trimmedChatHistory,
          activePromptCards,
          selectedModel
        );

        if (success) {
          // WebSocket Ïä§Ìä∏Î¶¨Î∞ç ÏÑ±Í≥µ, ÎÇòÎ®∏ÏßÄÎäî Î¶¨Ïä§ÎÑàÏóêÏÑú Ï≤òÎ¶¨
          return;
        } else {
          console.log("WebSocket Ï†ÑÏÜ° Ïã§Ìå®, SSE Ìè¥Î∞± Î™®ÎìúÎ°ú Ï†ÑÌôò");
        }
      } else {
        console.log("WebSocket ÎØ∏Ïó∞Í≤∞, SSE Î™®Îìú ÏÇ¨Ïö©");
      }

      // üåü Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ ÏãúÏä§ÌÖú Ïã§Ìñâ
      console.log("Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ ÏãúÏä§ÌÖú Ïã§Ìñâ ÏãúÏûë");

      // 1. Î®ºÏ†Ä ÌîÑÎ°¨ÌîÑÌä∏ Ïπ¥ÎìúÎì§ÏùÑ crew Ïù∏Ïä§ÌÑ¥Ïä§Î°ú ÏÉùÏÑ±
      if (activePromptCards.length > 0) {
        try {
          console.log("ÌîÑÎ°¨ÌîÑÌä∏ Ïπ¥Îìú ‚Üí ÌÅ¨Î£® Ïù∏Ïä§ÌÑ¥Ïä§ Î≥ÄÌôò ÏãúÎèÑ");
          await crewAPI.createCrewInstance(projectId, activePromptCards);
          console.log("ÌÅ¨Î£® Ïù∏Ïä§ÌÑ¥Ïä§ ÏÉùÏÑ± ÏôÑÎ£å");
        } catch (instanceError) {
          console.log("ÌÅ¨Î£® Ïù∏Ïä§ÌÑ¥Ïä§ ÏÉùÏÑ± Ïã§Ìå® (Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÍ±∞ÎÇò Í∏∞ÌÉÄ Ïù¥Ïú†):", instanceError.message);
          // Ïù¥ÎØ∏ ÏÉùÏÑ±ÎêòÏñ¥ ÏûàÏùÑ Ïàò ÏûàÏúºÎØÄÎ°ú Í≥ÑÏÜç ÏßÑÌñâ
        }
      }

      // 2. Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ Î≥ëÎ†¨ Ïã§Ìñâ
      const multiAgentResult = await crewAPI.executeMultiAgent(
        projectId, 
        userMessage.content,
        (progress) => {
          // ÏßÑÌñâÏÉÅÌô© ÏóÖÎç∞Ïù¥Ìä∏ (ÏòµÏÖò)
          console.log("Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ ÏßÑÌñâÏÉÅÌô©:", progress);
        }
      );

      console.log("Î©ÄÌã∞-ÏóêÏù¥Ï†ÑÌä∏ Ïã§Ìñâ ÏôÑÎ£å:", multiAgentResult);

      // 3. Í≤∞Í≥ºÎ•º UI Î©îÏãúÏßÄÎ°ú Î≥ÄÌôò
      const assistantMessage = processMultiAgentResponse(multiAgentResult);
      
      // 4. Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄÎ•º ÏµúÏ¢Ö Í≤∞Í≥ºÎ°ú ÍµêÏ≤¥
      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === streamingMessageIdRef.current
        );

        if (streamingMsgIndex !== -1) {
          updatedMessages[streamingMsgIndex] = assistantMessage;
        } else {
          updatedMessages.push(assistantMessage);
        }

        return updatedMessages;
      });

      streamingMessageIdRef.current = null;
      setCanSendMessage(true);
      
      // ÏÑ±Í≥µ ÌÜ†Ïä§Ìä∏
      toast.success(`${Object.keys(multiAgentResult.agentResults || {}).length}Í∞ú ÏóêÏù¥Ï†ÑÌä∏ Î∂ÑÏÑù ÏôÑÎ£å!`);
    } catch (error) {
      console.error("Î©îÏãúÏßÄ Ï†ÑÏÜ° Ïã§Ìå®:", error);

      // Í∞úÏÑ†Îêú Ïò§Î•ò Î©îÏãúÏßÄ
      const errorType = error.code === "ECONNABORTED" ? "timeout" : "general";
      const errorMessage = {
        id: "error-" + Date.now(),
        type: "assistant",
        content:
          errorType === "timeout"
            ? "Ï≤òÎ¶¨ ÏãúÍ∞ÑÏù¥ Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§. ÏöîÏ≤≠Ïù¥ Î≥µÏû°ÌïòÍ±∞ÎÇò ÏÑúÎ≤ÑÍ∞Ä Î∞îÏÅú ÏÉÅÌÉúÏùº Ïàò ÏûàÏäµÎãàÎã§. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî."
            : "Î©îÏãúÏßÄ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. ÎÑ§Ìä∏ÏõåÌÅ¨ Ïó∞Í≤∞ÏùÑ ÌôïÏù∏ÌïòÍ≥† Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.",
        timestamp: new Date(),
        isError: true,
        errorDetails: {
          type: errorType,
          message: error.message,
          status: error.response?.status,
        },
      };

      setMessages((prev) => {
        // Ïä§Ìä∏Î¶¨Î∞ç Î©îÏãúÏßÄÎ•º Ï∞æÏïÑ Ï†úÍ±∞
        const currentStreamingId = streamingMessageIdRef.current;
        const filteredMessages = prev.filter(
          (msg) => msg.id !== currentStreamingId
        );
        return [...filteredMessages, errorMessage];
      });

      streamingMessageIdRef.current = null;

      // Ïò§Î•ò Î∞úÏÉù ÏãúÎèÑ ÏûÖÎ†• ÌôúÏÑ±Ìôî
      setCanSendMessage(true);
    }

    // Ï†ÑÏ≤¥ Ï†ÑÏÜ° Í≥ºÏ†ï ÏôÑÎ£å ÌõÑ ÏûÖÎ†• ÌôúÏÑ±Ìôî (Î≥¥ÌóòÏö©)
    setCanSendMessage(true);
  }, [
    inputValue,
    isGenerating,
    executeOrchestration,
    handleStreamingResponse,
    handleStreamingComplete,
    messages,
  ]);

  /**
   * Enter ÌÇ§Î°ú Ï†ÑÏÜ°
   */
  const handleKeyPress = useCallback(
    (e) => {
      if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();
        handleSendMessage();
      }
    },
    [handleSendMessage]
  );

  /**
   * Î©îÏãúÏßÄ Î≥µÏÇ¨
   */
  const handleCopyMessage = useCallback(async (content, messageId) => {
    const success = await copyToClipboard(content);
    if (success) {
      setCopiedMessage(messageId);
      setTimeout(() => setCopiedMessage(null), 2000);
    }
  }, []);

  /**
   * Í∞úÎ≥Ñ Ï†úÎ™© Î≥µÏÇ¨
   */
  const handleCopyTitle = useCallback(async (title, messageId, index) => {
    const success = await copyToClipboard(title, "Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§!");
    if (success) {
      setCopiedMessage(`${messageId}_title_${index}`);
      setTimeout(() => setCopiedMessage(null), 2000);
    }
  }, []);

  /**
   * Ï±ÑÌåÖ Ï¥àÍ∏∞Ìôî
   */
  const resetChat = useCallback(() => {
    setMessages([]);
    setInputValue("");
    setCopiedMessage(null);
    setCanSendMessage(true);
    streamingMessageIdRef.current = null;
    currentWebSocketRef.current = null;
    currentExecutionIdRef.current = null;
    resetOrchestration();
  }, [resetOrchestration]);

  return {
    messages,
    inputValue,
    setInputValue,
    handleInputChange, // ÏÉàÎ°úÏö¥ ÏûÖÎ†• Ìï∏Îì§Îü¨
    copiedMessage,
    isGenerating,
    isStreaming,
    canSendMessage,
    streamingMessageId: streamingMessageIdRef.current,
    messagesEndRef,
    inputRef,
    inputHeight, // ÎèôÏ†Å ÎÜíÏù¥
    handleSendMessage,
    handleStopGeneration,
    handleKeyPress,
    handleCopyMessage,
    handleCopyTitle,
    resetChat,
    scrollToBottom,
    // WebSocket ÏÉÅÌÉú Ï∂îÍ∞Ä
    wsConnected,
    wsConnecting,
    wsError,
    // Ïä§ÌÅ¨Î°§ Í¥ÄÎ†® Ï∂îÍ∞Ä
    scrollContainerRef,
    handleScroll,
    isUserScrolling,
    // Î™®Îç∏ ÏÑ†ÌÉù Í¥ÄÎ†® Ï∂îÍ∞Ä
    selectedModel,
    setSelectedModel,
  };
};
