import React, { useState, useEffect } from "react";
import { promptCardAPI } from "../services/api";
import ChatInterface from "./ChatInterface";
import LoadingSpinner from "./LoadingSpinner";
import { ChatInterfaceSkeleton } from "./skeleton/SkeletonComponents";

const UserView = ({ projectId, projectName }) => {
  const [promptCards, setPromptCards] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadPromptCards = async () => {
      try {
        setLoading(true);
        // AdminViewÏôÄ ÎèôÏùºÌïòÍ≤å includeContent=trueÎ°ú ÏÑ§Ï†ïÌïòÏó¨ ÌîÑÎ°¨ÌîÑÌä∏ ÎÇ¥Ïö©ÏùÑ Ìè¨Ìï®ÌïòÏó¨ Î°úÎìú
        const response = await promptCardAPI.getPromptCards(projectId, true);
        // ÏùëÎãµ Íµ¨Ï°∞Í∞Ä AdminViewÏôÄ ÎèôÏùºÌïòÍ≤å Ï≤òÎ¶¨
        setPromptCards(response.promptCards || []);
      } catch (error) {
        console.warn("ÌîÑÎ°¨ÌîÑÌä∏ Ïπ¥Îìú Î°úÎìú Ïã§Ìå®:", error);
        setPromptCards([]);
      } finally {
        setLoading(false);
      }
    };

    loadPromptCards();
  }, [projectId]);

  if (loading) {
    return <ChatInterfaceSkeleton />;
  }

  return (
    <div className="h-[calc(100vh-4rem)] bg-white dark:bg-dark-primary transition-colors duration-300">
      <ChatInterface
        projectId={projectId}
        projectName={projectName}
        promptCards={promptCards}
      />
    </div>
  );
};

export default UserView;
